.description {
  form {
    display: flex;

    flex-wrap: wrap;
    justify-content: center;
  }
}

.description__p {
  position: absolute;
  z-index: 2;

  width: 100%;
  height: 100%;
  margin: 0;

  font-size: 6.4rem;

  color: white;
  background-color: rgba(0, 0, 0, 0.4);

  span {
    display: block;

    width: 60%;
    max-width: 800px;
    padding: 0 6%;
    padding-top: 10%;
  }
}

.description__form {
  position: relative;
  z-index: 3;
  bottom: 102px;

  padding: 20px 0;

  background-color: rgba(0, 0, 0, 0.5);

  @media (max-width: 1052px) {
    position: initial;
  }
}

.description__label {
  display: flex;

  width: 24%;
  min-width: 320px;
  margin-right: 30px;
  padding: 20px 30px;

  border: none;
  background: rgba(255, 255, 255, 0.15);

  span {
    margin-right: 10px;
  }

  &:hover {
    background: rgba(255, 255, 255, 0.25);
  }

  .datepick {
    display: inline-block;
  }
}

.description__input {
  width: 68%;

  color: $white;
  border: none;
  outline: none;
  background: transparent;

  &:active,
  &:focus {
    border-bottom: solid 1px white;
    background: transparent;
  }
}

.description__button {
  min-width: 191px;
  padding: 18px 20px;

  cursor: pointer;
  letter-spacing: 0.03em;

  font-size: 24px;
  font-weight: 600;

  color: $black;
  border: none;
  outline: none;
  background-image: linear-gradient(45deg, #ff8008, #ffc837);

  &:hover,
  &:focus {
    background-image: linear-gradient(45deg, #ffc837, #ff8008);
  }
}

@media (max-width: 1240px) {
  .description__label {
    margin-bottom: 15px;
  }

  .description__button {
    margin-bottom: 15px;
    margin-left: 15px;
  }

  .description__p {
    font-size: 5rem;
  }
}

@media (max-width: 920px) {
  .description__p {
    font-size: 3rem;
  }
}

@media(max-width: 700px) {
  .description__button {
    width: 60%;
    margin: 0;
  }

  .description__label {
    width: 60%;
    margin: 0;
    margin-bottom: 15px;
  }
}
