.contacts {
  margin-bottom: 250px;
}

.contacts__items {
  display: flex;

  margin: 0 auto;
  padding: 0;
  padding: 3% 7%;

  list-style: none;

  border: solid 1px $white;

  flex-wrap: wrap;
  justify-content: space-around;
}

.contacts__item {
  position: relative;

  max-width: 410px;
  padding-left: 68px;

  &::before {
    position: absolute;
    top: 10px;
    left: 0;

    width: 50px;
    height: 50px;

    content: '';
  }

  &.contacts__item--phone::before {
    background: url('../img/phone-contact.svg') 0 0 no-repeat;
  }

  &.contacts__item--address::before {
    background: url('../img/address.svg') 0 0 no-repeat;
  }

  &.contacts__item--time {
    .contacts__item--mail {
      position: initial;

      padding-left: 0;

      &::before {
        top: 110px;

        background: url('../img/mail.svg') 0 0 no-repeat;
      }
    }

    &::before {
      background: url('../img/future.svg') 0 0 no-repeat;
    }
  }
}

.contacts__text {
  font-size: 18px;
  line-height: 30px;

  span {
    display: block;
  }
}

.contacts__link {
  display: block;

  margin-top: 10px;

  text-decoration: none;

  font-size: 30px;

  color: $white;
}

@media (max-width: 920px) {
  .contacts {
    margin-bottom: 0;
  }
}

@media (max-width: 832px) {
  .contacts__item {
    width: 100%;
    max-width: auto;
    padding-left: 60px;

    &::before {
      top: 15px;

      width: 35px;
      height: 35px;
    }
  }

  .contacts__text {
    font-size: 14px;
  }
}
