.services {
  margin-bottom: 130px;
}

.services__heading {
  margin: 10px 0;
  margin-bottom: 100px;
}

.services__list {
  display: flex;

  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;

  flex-wrap: wrap;
  justify-content: space-around;
}

.services__item {
  max-width: 355px;
  margin: 0 10px;

  text-align: center;
}

.services__name {
  font-size: 30px;
  font-weight: 700;
}

.services__icon {
  fill: #e0901b;
}

@media (max-width: 920px) {
  .services {
    margin-bottom: 30px;
  }

  .services__item {
    max-width: 305px;
  }

  .services__name {
    font-size: 23px;
  }

  .services__text {
    font-size: 15px;
  }

  .services__heading {
    margin-bottom: 25px;
  }
}
