.main-header a:hover,
.main-header a:focus {
  opacity: 0.7;
}

.main-header__container {
  display: flex;

  max-width: 1691px;
  margin: 0 auto;
  padding: 16px 30px;

  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-header__menu-button--hidden {
  display: none;
}

.main-header__logo {
  width: 188px;
  height: 79px;
  margin-bottom: 15px;
}

.main-header__list {
  display: flex;

  margin: 0 20px;
  padding: 0;

  list-style: none;

  align-items: center;
  flex-wrap: wrap;
}

.main-header__item {
  margin-right: 90px;

  &:last-of-type {
    margin-right: 0;
  }
}

.main-header__link {
  text-decoration: none;
  letter-spacing: 0.15em;

  color: $white;

  &.active {
    color: #ffc837;
  }
}

.main-header__tel {
  position: relative;

  padding-left: 40px;

  text-decoration: none;
  letter-spacing: 0.05em;

  font-family: $helvetica;
  font-size: 30px;
  font-weight: 400;

  color: $white;

  &::before {
    position: absolute;
    left: 0;

    width: 27px;
    height: 27px;

    content: '';

    background: url('../img/telephone.png') 0 0 no-repeat;
  }
}

@media(max-width: 920px) {
  .main-header__container {
    justify-content: center;
  }

  .main-header__list {
    margin: 15px 20px 35px 20px;
  }
}

@media (max-width: 730px) {
  .main-header__list {
    width: 100%;
  }

  .main-header__item {
    width: 100%;
    margin: 0;
    margin-bottom: 10px;

    text-align: center;
  }
}
