.big-slider {
  position: relative;

  overflow: hidden;

  width: 100%;
  height: 100%;

  .swiper-slide {
    position: relative;

    overflow: hidden;

    width: 100%;
    height: auto;
    padding-top: 51%;

    img {
      position: absolute;
      top: 50%;
      left: 50%;

      display: block;

      width: 100%;
      min-width: 100%;
      min-height: 100%;

      transform: translate(-50%, -50%);

      object-fit: cover;
    }
  }
}
