$width: 15.625em;
$radius: $width * 0.016891;
$transition: 0.3s;
$font-size: $width * 0.056;
$lightblue: lightblue;

/*
  All a user has to do to change the calendar size is
  change the font-size on the container and everything
  magically resizes accordingly. Relative units ftw!
*/

.qs-datepicker-container {
  position: absolute;
  z-index: 9001;

  display: flex;
  overflow: hidden;
  flex-direction: column;

  width: $width;

  user-select: none;

  font-family: sans-serif;
  font-size: 1rem;

  color: black;
  border: 1px solid gray;
  border-radius: $radius;
  background: white;
  box-shadow: 0 ($width * 0.08) ($width * 0.08) ($width * -0.06) rgba(0, 0, 0, 0.3);

  * {
    box-sizing: border-box;
  }
}

.qs-centered {
  position: fixed;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

.qs-hidden {
  display: none;
}

.qs-overlay {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;
  padding: 0.5em;

  transition: opacity $transition;

  opacity: 1;
  color: white;
  background: rgba(0, 0, 0, 0.75);

  &.qs-hidden {
    z-index: -1;

    opacity: 0;
  }

  .qs-overlay-year {
    display: block;

    width: 80%;
    margin: 0 auto;
    padding: 0.25em 0;

    text-align: center;

    font-size: $font-size;

    color: white;
    // Overlay year input element.
    border: none;
    border-bottom: 1px solid white;
    border-radius: 0;
    background: transparent;

    // https://goo.gl/oUuGkG
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
  }

  .qs-close {
    position: absolute;
    top: 0;
    right: 0;

    padding: 0.5em;

    cursor: pointer;
  }

  .qs-submit {
    margin: 0 auto auto;
    padding: 0.5em;

    cursor: pointer;

    border: 1px solid white;
    border-radius: $radius;
    background: rgba(128, 128, 128, 0.4);

    &.qs-disabled {
      cursor: not-allowed;

      color: gray;
      border-color: gray;
    }
  }

  .qs-overlay-month-container {
    display: flex;

    flex-wrap: wrap;
    flex-grow: 1;
  }

  .qs-overlay-month {
    display: flex;

    width: calc(100% / 3);

    cursor: pointer;
    transition: opacity $transition / 2;

    opacity: 0.5;

    justify-content: center;
    align-items: center;

    &.active,
    &:hover {
      opacity: 1;
    }
  }
}

.qs-controls {
  display: flex;

  width: 100%;

  transition: filter $transition;

  background: lightgray;

  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 0;
  filter: blur(0px);

  &.qs-blur {
    filter: blur(5px);
  }
}

.qs-arrow {
  position: relative;

  width: $width / 10;
  height: $width / 10;

  cursor: pointer;
  transition: background 0.15s;

  border-radius: $radius;

  &:hover {
    background: rgba(0, 0, 0, 0.1);

    &.qs-left:after {
      border-right-color: black;
    }

    &.qs-right:after {
      border-left-color: black;
    }
  }

  &:after {
    position: absolute;
    top: 50%;

    content: '';
    transition: border 0.2s;

    border: ($width / 40) solid transparent;
  }

  &.qs-left:after {
    right: 50%;

    transform: translate(25%, -50%);

    border-right-color: gray;
  }

  &.qs-right:after {
    left: 50%;

    transform: translate(-25%, -50%);

    border-left-color: gray;
  }
}

.qs-month-year {
  cursor: pointer;
  transition: border 0.2s;

  font-weight: bold;

  border-bottom: 1px solid transparent;

  &:hover {
    border-bottom: 1px solid gray;
  }

  &:focus,
  &:active:focus {
    outline: none;
  }
}

.qs-month {
  padding-right: 0.5ex;
}

.qs-year {
  padding-left: 0.5ex;
}

.qs-squares {
  display: flex;

  padding: $width * 0.02;

  transition: filter $transition;

  flex-wrap: wrap;
  filter: blur(0px);

  &.qs-blur {
    filter: blur(5px);
  }
}

.qs-square {
  display: flex;

  width: calc(100% / 7);
  height: $width / 10;

  cursor: pointer;
  transition: background 0.1s;
  // Overriden for date-range dates below.

  border-radius: $radius;

  align-items: center;
  justify-content: center;

  &:not(.qs-empty):not(.qs-disabled):not(.qs-day):not(.qs-active) {
    &:hover {
      background: orange;
    }
  }
}

// Today's date
.qs-current {
  text-decoration: underline;

  font-weight: bold;
}

/*
  3 possibilities:
    1. Single, active date.
    2. Daterange start selection.
    3. Daterange end selection.
*/

.qs-active,
.qs-range-start,
.qs-range-end {
  background: $lightblue;
}

// Daterange start selection.
.qs-range-start {
  &:not(.qs-range-6) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
}

// Daterange middle selections.
.qs-range-middle {
  background: lighten($lightblue, 10%);

  &:not(.qs-range-0):not(.qs-range-6) {
    border-radius: 0;
  }

  &.qs-range-0 {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  &.qs-range-6 {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}

// Daterange end selection.
.qs-range-end {
  &:not(.qs-range-0) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}

.qs-disabled,
.qs-outside-current-month {
  opacity: 0.2;
}

.qs-disabled {
  cursor: not-allowed;
}

.qs-empty {
  cursor: default;
}

.qs-day {
  cursor: default;

  font-weight: bold;

  color: gray;
}

.qs-event {
  position: relative;

  &:after {
    position: absolute;
    right: 0;
    bottom: 0;

    width: $width * 0.03;
    height: $width * 0.03;

    content: '';

    border-radius: 50%;
    background: #0077ff;
  }
}
