.price {
  margin-bottom: 206px;
}

.price__heading {
  margin-bottom: 145px;
}

.price__choose-list {
  display: flex;

  margin: 0;
  margin-bottom: 106px;
  padding: 0;

  list-style: none;

  flex-wrap: wrap;
  justify-content: space-between;
}

.price__choose-item {
  width: calc(25% - 35px);
  min-width: 190px;
  margin-bottom: 15px;
  padding: 15px 0;

  cursor: pointer;
  text-align: center;

  font-size: 30px;

  border: solid 1px $white;

  &:last-of-type {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    border-color: #ffad25;
  }
}

@media (max-width: 920px) {
  .price {
    margin-bottom: 25px;
  }

  .price__choose-item {
    font-size: 20px;
  }

  .price__choose-item {
    width: 49%;
  }

  .price__heading {
    margin-bottom: 25px;
  }

  .price__choose-list {
    margin-bottom: 15px;
  }
}

@media (max-width: 410px) {
  .price__choose-item {
    width: 100%;
  }
}
