.table {
  width: 100%;

  border-collapse: collapse;

  border: solid 1px #4d4d4d;

  th {
    padding: 35px;

    border: 1px solid #4d4d4d;
  }

  .table__heading {
    text-align: left;
    text-transform: uppercase;

    font-size: 30px;
  }

  td {
    padding: 7% 4%;

    text-align: center;

    font-size: 36px;

    border: 1px solid #4d4d4d;
    border-top: 2px solid $white;
    border-bottom: 2px solid $white;
  }

  .table__description {
    padding: 7% 2%;

    text-align: left;
    text-transform: uppercase;

    font-size: 24px;

    span {
      display: block;

      margin-top: 30px;

      text-transform: initial;
    }
  }
}

@media (max-width: 1270px) {
  .table {
    td {
      padding: 10px 20px;

      font-size: 20px;

      &:first-of-type {
        padding: 20px 10px;

        font-size: 17px;
      }
    }

    th {
      padding: 10px;
    }
  }
}

@media only screen and (max-width: 760px),
(min-device-width: 762px) and (max-device-width: 1024px) {
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block;
  }

  .table {
    thead tr {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }

    tr {
      border: 1px solid #cccccc;
    }

    td {
      position: relative;

      padding-left: 50%;
      /* Behave  like a "row" */

      border: none;
      border-bottom: 1px solid #eeeeee;
    }

    td:before {
      /* Now like a table header */
      position: absolute;
      /* Top/left values mimic padding */
      top: 6px;
      left: 6px;

      width: 45%;
      padding-right: 10px;

      white-space: nowrap;
    }

    th {
      display: none;

      &:first-of-type {
        display: block;
      }
    }

    td:nth-of-type(2):before {
      content: 'Категория 1';
    }

    td:nth-of-type(3):before {
      content: 'Категория 2';
    }

    td:nth-of-type(4):before {
      content: 'Категория 3';
    }

    td:nth-of-type(5):before {
      content: 'Категория 4';
    }

    td:nth-of-type(6):before {
      content: 'Категория 5';
    }

    .table__heading,
    .table__description {
      text-align: center;
    }
  }
}
