.main-slider {
  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    width: 60%;
    max-width: 1300px;

    text-align: center;

    font-size: 18px;

    background: #ffffff;

    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-pagination {
    position: relative;
  }

  &.swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 0;
    left: 0;

    display: flex;

    padding: 90px 0;

    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .swiper-pagination-bullet {
      width: 20px;
      height: 20px;
      margin: 0 20px;

      background-color: #af9a9a;

      &-active {
        width: 27px;
        height: 27px;

        background-color: #ffc736;
      }
    }
  }
}

.main-slider__button-next {
  position: absolute;
  z-index: 2;
  top: 29%;
  right: 6%;

  width: 100px;
  height: 100px;

  cursor: pointer;

  border-radius: 50%;
  outline: none;
  background: url('../img/arrow.png') center center no-repeat;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.5);

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.main-slider__button-prev {
  position: absolute;
  z-index: 2;
  top: 29%;
  left: 6%;

  width: 100px;
  height: 100px;

  cursor: pointer;
  transform: rotate(180deg);

  border-radius: 50%;
  outline: none;
  background: url('../img/arrow.png') center center no-repeat;
  box-shadow: -1px -3px 5px rgba(0, 0, 0, 0.5);

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

@media (max-width: 1024px) {
  .main-slider__button-next,
  .main-slider__button-prev {
    display: none;
  }
}

@media (max-width: 920px) {
  .main-slider.swiper-container-horizontal > .swiper-pagination-bullets {
    display: none;
  }
}
